import React, { useState } from "react";
import "./Modal.css";
import img1 from "./4.jpg";
export default function Modal(props) {
  const [modal, setModal] = useState(false);

  const toggleModal = () => {
    setModal(!modal);
  };

  /* Prevents scrolling while modal is open */
  if (modal) {
    document.body.classList.add("active-modal");
  } else {
    document.body.classList.remove("active-modal");
  }

  const { image, titel, p1, p2, link } = this.props;

  return (
    <>
      <button
        onClick={toggleModal}
        className="open-modal"
      >
        <img
          src={require(`${image}`)}
          alt="product"
        />
      </button>

      {/* If modal is true, return modal */}
      {modal && (
        <div className="modal">
          <div
            onClick={toggleModal}
            className="overlay"
          ></div>
          <div className="modal-content">
            <h2>{titel}</h2>
            <p>{p1}</p>
            <p>{p2}</p>
            <a
              href={link}
              target="_blank"
            >
              Här
            </a>
            <button
              className="close-modal"
              onClick={toggleModal}
            >
              CLOSE
            </button>
          </div>
        </div>
      )}
    </>
  );
}
