import "./Footer.css";
import React, { useState } from "react";
import img1 from "../assets/backgrounds/2.jpg";
import img2 from "../assets/backgrounds/karim-ben-van-mKk2nnZ1EQk-unsplash.jpg";

export default function Footer() {
  const [imageSrc, setImageSrc] = useState(img1);

  // Function to change the image source
  const changeImageSrc = () => {
    setImageSrc(img2);
  };

  if (imageSrc) {
    document.body.classList.add("active-modal");
  } else {
    document.body.classList.remove("active-modal");
  }

  return (
    <div className="footer-container">
      <p>Emma</p>
      <button onClick={changeImageSrc}>ändra bild här</button>
      <img
        style={{ width: "20px", height: "auto" }}
        src={imageSrc}
        alt="bild"
      ></img>
    </div>
  );
}
