import React from "react";
import Modal from "../components/Modal/Modal.js";
import modalData from "../components/Modal/Modal.json";
import img1 from "../assets/emma-gotland.jpg";
import img2 from "../assets/resumebild.jpg";
import img3 from "../assets/linkedIn.png";

export default function Portfolio() {
  return (
    <>
      <h1>Portfolio</h1>
      {modalData.Modals.map((m) => (
        <div className="modals">
          <div key={m.id}>
            <Modal
              titel={m.title}
              p1={m.paragraph1}
              p2={m.paragraph2}
              link={m.link}
              image={m.image}
            ></Modal>
          </div>
        </div>
      ))}
    </>
  );
}
